import { Component, h, Listen, State } from "@stencil/core";


@Component({
    tag: "wxcc-time",
    styleUrl: "wxcc-time.css",
    shadow: true

})

export class wxccTime {
    @State() td = new Date()
    @State() fd = new Date()
    @State() toSelected: HTMLSelectElement
    @State() fromSelected: HTMLSelectElement
    @State() tTime: HTMLInputElement
    @State() tDate: HTMLInputElement
    @State() fTime: HTMLInputElement
    @State() fDate: HTMLInputElement
    @State() to: number
    @State() from: number
    @State() hide = true

    onToSelect(zzz: string) {
        console.log(zzz)
        if (zzz === "from") {
            if (!this.fromSelected.value) {
                console.log(this.fDate.value + "T" + this.fTime.value)
                this.fd = new Date(this.fDate.value + "T" + this.fTime.value)

            } else if (this.fromSelected.value === "now") {
                this.fd = new Date(Date.now())
            } else {
                this.fd = new Date(Date.now() - +this.fromSelected.value)
            }
            // this.fDate.value = this.fd.toISOString().slice(0, 10)
            let month = ("0" + (this.fd.getMonth() + 1)).slice(-2, 3)
            let date = ("0" + (this.fd.getDate())).slice(-2, 3)
            // console.log(month)
            this.fDate.value = (this.fd.getFullYear() + "-" + month + "-" + date)
            this.fTime.value = this.fd.toTimeString().slice(0, 8)
            this.from = this.fd.valueOf() as number
        } else {
            if (!this.toSelected.value) {
                this.td = new Date(this.tDate.value + "T" + this.tTime.value)
            } else if (this.toSelected.value === "now") {
                this.td = new Date(Date.now())
            } else {
                this.td = new Date(Date.now() - +this.toSelected.value)
            }
            // this.tDate.value = this.td.toISOString().slice(0, 10)
            let month = ("0" + (this.td.getMonth() + 1)).slice(-2, 3)
            let date = ("0" + (this.td.getDate())).slice(-2, 3)
            // console.log(month)
            this.tDate.value = (this.td.getFullYear() + "-" + month + "-" + date)
            this.tTime.value = this.td.toTimeString().slice(0, 8)
            this.to = this.td.valueOf() as number
            // console.log(this.td.getDate())
            
        }
    }

    onManSet(zzz: string) {
        if (zzz === "from") {
            this.fromSelected.value = null
        } else {
            this.toSelected.value = null
        }
        this.onToSelect(zzz)
    }

    @Listen('wxccTimeWidget', { target: "body" })
    onToggle() {
        this.hide = !this.hide
    }

    render() {

        return [
            <div class={"frame" + (this.hide ? " hidden" : "")}>
                <header><span class="hideMe" onClick={this.onToggle.bind(this)}>Hide</span><div class="title">Time Widget</div></header>
                <section class="widgets">
                    <div>
                        <label id="from">From: </label>
                        <input type="date" onChange={this.onManSet.bind(this, "from")} ref={el => this.fDate = el}></input><input type="time" onChange={this.onManSet.bind(this, "from")} ref={el => this.fTime = el} ></input>
                        <select ref={el => this.fromSelected = el} onChange={this.onToSelect.bind(this, "from")}>
                            <option></option>
                            <option value="600000">10 minutes ago</option>
                            <option value="3600000">1 hour ago</option>
                            <option value="86400000">1 day ago</option>
                            <option value="604800000">1 week ago</option>
                        </select>
                    </div>
                    <br></br>
                    <div>
                        <label id="to">To: </label>
                        <input type="date" onChange={this.onManSet.bind(this, "to")} ref={el => this.tDate = el}></input><input type="time" onChange={this.onManSet.bind(this, "to")} ref={el => this.tTime = el} ></input>
                        <select ref={el => this.toSelected = el} onChange={this.onToSelect.bind(this, "to")}>
                            <option></option>
                            <option value="now">Now</option>
                            <option value="600000">10 minutes ago</option>
                            <option value="3600000">1 hour ago</option>
                            <option value="86400000">1 day ago</option>
                            <option value="604800000">1 week ago</option>
                        </select>
                    </div>
                </section>
                <br></br>
                <section>
                    <p>
                        from: "{this.from}"
                        <br></br>
                        to: "{this.to}"
                    </p>

                </section>
            </div>

        ]
    }
}